@import url(https://fonts.googleapis.com/css?family=Chilanka|Oswald&display=swap&subset=latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  background: -webkit-linear-gradient(45deg, red, blue);
  background: linear-gradient(45deg, red, blue); }

.wrap {
  min-height: 100vh; }
  .wrap h1 {
    font-family: 'Chilanka', cursive;
    color: #cdc0c0;
    text-align: center;
    padding-top: 20px; }
  .wrap label {
    font-size: 23px;
    color: white; }
  .wrap input#important {
    width: 20px;
    height: 20px; }
  .wrap .task-list {
    background-color: #61009d;
    border: 2px solid white;
    color: #cdc0c0;
    padding: 10px;
    margin: 5px; }
    .wrap .task-list .single-task {
      -webkit-animation: task .1s .2s both;
              animation: task .1s .2s both; }
    .wrap .task-list .one-task,
    .wrap .task-list .free-time {
      font-size: 20px; }
    .wrap .task-list .task-done,
    .wrap .task-list .extra-info,
    .wrap .task-list .delete-task {
      font-size: 20px;
      padding: 5px;
      background: none;
      border: none;
      color: #cdc0c0;
      cursor: pointer; }
  .wrap .list {
    min-width: 50%;
    min-height: 50%;
    font-family: 'Oswald', Arial, sans-serif; }
    .wrap .list .wrapNewTask {
      max-width: 310px;
      margin: 10px auto; }
      .wrap .list .wrapNewTask .add {
        display: inline-block;
        color: #cdc0c0;
        margin-left: 10px;
        font-size: 25px; }
        .wrap .list .wrapNewTask .add .fa-plus-square {
          vertical-align: middle; }
    .wrap .list .newTask {
      font-size: 20px; }

div.blured {
  position: fixed;
  top: 0%;
  left: 0%;
  width: 100vw;
  height: 100vh;
  background-color: gray;
  opacity: 0.6;
  display: none; }

div.extras {
  display: none;
  background: rgba(231, 181, 181, 0.97);
  width: 80%;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 10px;
  border: 2px solid black;
  z-index: 2;
  box-shadow: 6px 6px black; }
  div.extras .fa-window-close {
    position: absolute;
    top: 7px;
    right: 7px;
    font-size: 20px;
    cursor: pointer;
    -webkit-transition: .2s;
    transition: .2s; }
  div.extras .fa-window-close:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }
  div.extras .addExtra {
    max-width: 40px;
    display: inline-block;
    font-size: 20px;
    margin-left: 3px; }
  div.extras .fa-plus-square {
    vertical-align: middle;
    cursor: pointer;
    -webkit-transition: .2s;
    transition: .2s; }
  div.extras .fa-plus-square:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }
  div.extras .extras-in {
    font-size: 15px;
    max-width: 180px; }

@-webkit-keyframes task {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes task {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@media (min-width: 319px) and (orientation: landscape) {
  div.extras {
    width: 70%; }
    div.extras .extras-in {
      max-width: 200px; } }

@media (min-width: 410px) and (orientation: portrait) {
  .wrap .list .newTask {
    font-size: 21px; } }

@media (min-width: 639px) and (orientation: landscape) {
  div.extras .extras-in {
    max-width: 360px;
    width: 360px;
    font-size: 21px; }
  div.extras .fa-plus-square {
    font-size: 28px; }
  div.extras .extras-par {
    font-size: 22px; } }

@media (min-width: 765px) {
  .wrap .list .newTask {
    font-size: 30px; }
  .wrap .list .wrapNewTask {
    max-width: 800px;
    text-align: center; }
    .wrap .list .wrapNewTask .add {
      font-size: 35px; } }

@media (min-width: 767px) and (orientation: portrait) {
  div.extras {
    top: 20%;
    min-height: 130px; }
    div.extras .extras-in {
      max-width: none;
      width: 440px;
      font-size: 25px; }
    div.extras .extras-par {
      font-size: 25px; }
    div.extras .fa-plus-square {
      vertical-align: -3px;
      font-size: 30px; }
    div.extras .fa-window-close {
      font-size: 30px; } }

@media (min-width: 1023px) {
  .wrap .list .newTask {
    max-width: 388px; }
  .wrap {
    max-width: 80vw;
    margin: 0 auto; }
  div.extras {
    top: 20%;
    min-height: 130px; }
    div.extras .extras-in {
      max-width: none;
      width: 440px;
      font-size: 25px; }
    div.extras .extras-par {
      font-size: 25px; }
    div.extras .fa-plus-square {
      vertical-align: -3px;
      font-size: 30px; }
    div.extras .fa-window-close {
      font-size: 30px; } }

